rgin:0 0.25em}.SearchCard .SearchCard__contents .SearchCard__description{font-size:0.88rem}.SidebarRow{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:0.5em 1em;transition:0.2s ease-in-out;color:#606060;cursor:pointer}.SidebarRow:hover:not(.active){background:#d9d9d9}.SidebarRow:hover:not(.active) .icon{color:#ff0000}.SidebarRow:hover:not(.active) .title{color:#000;font-weight:800}.SidebarRow .icon{color:#606060;margin-right:1.25em}.SidebarRow .title{font-size:0.875rem;margin:0;font-weight:400}.active{background:#e5e5e5;color:#000;font-weight:800}.active .icon{color:#ff0000}.active .title{font-weight:700}.active:hover{background:#d9d9d9}

